.about-hero {
  width: 100%;
  margin: 0 auto;
}

.hero {
  position: relative;
  color: #fff;
  text-align: center;
  background-image: url("../img/hero.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.hero h1 {
  color: #fff;
  font-size: 28px;
}
.hero p {
  font-size: 20px;
}
@media all and (min-width: 768px) {
  .hero {
    text-align: left;
  }
  .hero h1 {
    font-size: 48px;
  }
  .hero p {
    font-size: 24px;
  }
}
.hero .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(159, 61, 153, 0.7);
  z-index: 0;
}
.hero .hero-content {
  position: relative;
  z-index: 1;
  margin: 25% 0;
}